import { LanguageCodes } from 'constants/languages';
import { useTranslation } from 'react-i18next';

const Dashboard = () => {
  const { i18n } = useTranslation();
  const handleChangeLanguage = () => {
    let newLang = i18n.language === LanguageCodes.EN ? LanguageCodes.JP : LanguageCodes.EN;
    i18n.changeLanguage(newLang);
  };
  const { t } = useTranslation();
  return (
    <div>
      <h2 className="font-bold text-3xl text-center"> {t('Dashboard')}</h2>

      <div className='container'>
        <button onClick={handleChangeLanguage}>Change languages</button>
      </div>
    </div>
  );
};

export default Dashboard;
